import { countState } from '../types/count.type'
export const share = (f: countState, m: countState, d: number) => {
    const textEncoded = `
      date: ${new Date()}
      female: up ${f.up} down ${f.down}
      male: up ${m.up} down ${m.down}
      dead: ${d}
    `
    const href= `https://api.whatsapp.com/send?text=${encodeURIComponent(textEncoded)}`
    window.open(href)
  }