import React, { useState, useEffect } from 'react';
import { useSwipeable } from 'react-swipeable';
import './App.css';
import Card from './components/CounterWidget/CounterWidget';
import BtnIcon from './components/Buttons/BtnIcon'
import {action, state, partialState} from './types/count.type'
import {share} from './core/share'
const ls = localStorage;

const getStateObj = (f:any, m: any, d: any) => ({f,m,d})
const addSubtract = (a: action, v: number) => a === 'plus' ? v + 1 : v - 1;

function App() {

  const initialState = {
    f: {up: 0, down: 0}, 
    m: {up: 0, down: 0},
    d: 0
  }

  const [fCount, setFCount] = useState(initialState.f);
  const [mCount, setMCount] = useState(initialState.m);
  const [dCount, setDCount] = useState(initialState.d);
  const [loaded, setLoaded] = useState(false)
  const [menu, setMenu] = useState(false)


  const calc:  any = {
    f: ({a, d}: partialState) => ({ ...fCount, [d]: addSubtract(a, fCount[d]) }), 
    m: ({a, d}: partialState) => ({ ...mCount, [d]: addSubtract(a, mCount[d]) }),
    d: ({a}: partialState) => addSubtract(a, dCount)
  }

  const setState: any = getStateObj(setFCount, setMCount, setDCount)

  const saveState = () => {
    ls.setItem('f', JSON.stringify(fCount))
    ls.setItem('m', JSON.stringify(mCount))
    ls.setItem('d', JSON.stringify(dCount)) 
  }

  useEffect(() => {
    !!loaded && saveState() 
  })
  
  useEffect(() => {
    const getState = () => {
      const f = ls.getItem('f')
      const m = ls.getItem('m')
      const d = ls.getItem('d')    
  
      f && setState.f(JSON.parse(f))
      m && setState.m(JSON.parse(m))
      d && setState.d(JSON.parse(d))
    }

    !loaded && getState()
    setLoaded(true)
  }, [loaded, setState])

  const handlers = useSwipeable({
    onSwipedLeft: () => setMenu(false)
  });

  const count = ({a, d, t}: state) => {
    setState[t](calc[t]({a , d}))   
  }

  const reset = () => {
    setState.f(initialState.f)
    setState.m(initialState.m)
    setState.d(initialState.d)
  }

  const _share = () => {
    share(fCount, mCount, dCount);
  }

  return (
    <div className="App">
      <div className="MenuBtn">
        <BtnIcon iconName="icon-Icon-awesome-hamburger" onClick={() => setMenu(!menu)}/>
      </div>
      <div {...handlers} className="Menu" data-open={menu}>
        <div onClick={() => setMenu(!menu)} className="Menu__Close" ></div>
        <ul>
          <li onClick={_share}>Share</li>
          <li onClick={reset}>Reset</li>
        </ul>
      </div>

      <Card 
        type="f"
        vUp={fCount.up}
        vDown={fCount.down}
        oCUpPlus={() => count({a: 'plus', d: "up", t: 'f'})}
        oCUpMinus={() => count({a: 'minus', d: "up", t: 'f'})}
        oCDownPlus={() => count({a: 'plus', d: "down", t: 'f'})}
        oCDownMinus={() => count({a: 'minus', d: "down", t: 'f'})}
      />
      <div className="Card Card--d">
        <BtnIcon iconName="icon-Icon-awesome-plus-circle" onClick={() => count({a: 'plus', t: 'd', d: 'up'})} />
        <div className="Card__Value">{dCount}</div> 
        <BtnIcon iconName="icon-Icon-awesome-minus-circle" onClick={() => count({a: 'minus', t: 'd', d: 'up'})} />
      </div>
      <Card
        type="m"
        vUp={mCount.up}
        vDown={mCount.down}
        oCUpPlus={() => count({a: 'plus', d: "up", t: 'm'})}
        oCUpMinus={() => count({a: 'minus', d: "up", t: 'm'})}
        oCDownPlus={() => count({a: 'plus', d: "down", t: 'm'})}
        oCDownMinus={() => count({a: 'minus', d: "down", t: 'm'})}
      />
    </div>
  );
}

export default App;
