import React from 'react'
import './CounterWidget.css'
import Separator from '../Separator/Separator'
import Row from '../Row/Row'
import BtnIcon from '../Buttons/BtnIcon'

type Props = {
  type: string,
  vUp: number,
  vDown: number,
  oCUpPlus: Function,
  oCUpMinus: Function,
  oCDownPlus: Function,
  oCDownMinus: Function
}

function Card({type, vUp, vDown, oCUpPlus, oCUpMinus, oCDownPlus, oCDownMinus}: Props) {
  const className = `Card Card--${type}`
  const MainIconName =  `icon-Icon-ionic-md-${(type === 'f' ? `female`: 'male')}`
  return (
    <div className={className}>
          <div className="Icon Icon-Main">
              <div className={MainIconName} />
          </div>
          <div className="Card__Actions">
              <Row>
                  <div className="Icon">
                      <div className="icon-mountain" />
                  </div>
                  <div className="Card__Value">{vUp}</div>
                  <div className="Btns-group">
                      <BtnIcon iconName="icon-Icon-awesome-minus-circle" onClick={() => oCUpMinus()} />
                      <BtnIcon iconName="icon-Icon-awesome-plus-circle" onClick={() => oCUpPlus()} />
                  </div>
              </Row>
              <Separator />
              <div className="Row">
                  <div className="Icon">
                      <div className="icon-Icon-awesome-water" style={{paddingLeft: '5px'}}/>
                  </div>
                  <div className="Card__Value">{vDown}</div>
                  <div className="Btns-group">
                    	<BtnIcon iconName="icon-Icon-awesome-minus-circle" onClick={() => oCDownMinus()} />
                      <BtnIcon iconName="icon-Icon-awesome-plus-circle" onClick={() => oCDownPlus()} />
                  </div>
              </div>
          </div>
      </div>
  )
}

export default Card
